const store = {
	wholeStoreSubs: [],
};

function getAllStoreData() {
	const storeValues = {};

	for (let key in store) {
		if (store[key].data) {
			storeValues[key] = store[key].data;
		}
	}

	return storeValues;
}

function saveToLocalStorage() {
	const storeValues = JSON.stringify(getAllStoreData());

	window.localStorage.setItem('idlestore', storeValues);
}

function initStoreValue(key, data, subs=[]) {
	if (store[key]) {
		console.error('Store already has entry by key', key);
		return;
	}

	const storeValues = JSON.parse(window.localStorage.getItem('idlestore'));

	if (storeValues && storeValues[key]) {
		data = storeValues[key];
	}

	store[key] = {
		data: data,
		subs: [...subs]
	};
}

function getStoreValue(key, prop) {
	if (!store[key]) {
		console.error('No entry in store by key', key);
		return null;
	}

	return store[key].data[prop];
}

function executeSubs(key, prop) {
	if (!store[key]) {
		console.error('No entry in store by key', key);
		return;
	}

	const subs = store[key].subs;

	if (subs.length == 0) {
		return;
	} 

	for (let sub in subs) {
		if (subs[sub].triggers.indexOf(prop) > -1) {
			subs[sub].func();
		}
	}

	return;
}

function updateStoreValue(key, prop, value) {
	if (!store[key]) {
		console.error('No entry in store by key', key);
		return;
	}

	store[key].data[prop] = value;
	executeSubs(key, prop);
	executeWholeStoreSubs();

	return;
}

function updateStoreSubs(key, subs) {
	if (!store[key]) {
		console.error('No entry in store by key', key);
		return;
	}

	store[key].subs.push(...subs);

	return;
}

function replacer(key, value) {
	if (typeof value === 'function') {
		return value.toString();
	}
	return value;
}

function getStoreString(seperator='') {
	return JSON.stringify(store, replacer, seperator);
}

function updateWholeStoreSubs(subs) {
	store.wholeStoreSubs.push(...subs);
}

function executeWholeStoreSubs() {
	const subs = store.wholeStoreSubs;

	if (subs.length == 0) {
		return;
	} 

	for (let sub in subs) {
		subs[sub]();
	}

	return;
}

export {
	initStoreValue,
	getStoreValue,
	updateStoreValue,
	updateStoreSubs,
	getStoreString,
	updateWholeStoreSubs,
	saveToLocalStorage
};
