import { draw, createAccumulatorInterface } from '/utils/display.js';
import { onlineTicks } from '/utils/tick.js';
import { getStoreValue, updateStoreValue, initStoreValue } from '/utils/store.js';

import { MINIMUM_INTERVAL } from '/utils/globals.js';

function startButton() {
	document.querySelector('.startCounter').addEventListener('click', function () {
		updateStoreValue('exit', false);

		createAccumulator('sample', 2000, 2);
		createAccumulator('sample_2', 10000, 10);
		createAccumulator('sample_3', 500, 1);

		document.querySelector('.startCounter').disabled = true;
		document.querySelector('.stopCounter').disabled = false;
	});

	return;
}

function calculateOutput(name, resource='$') {
	const interval = getStoreValue(name, 'interval'),
		rate = getStoreValue(name, 'rate');

	let intervalString,
		rateString;

	if (interval === MINIMUM_INTERVAL) {
		intervalString = 'tick';
	} else {
		intervalString = interval / 1000.0 + ' second' + (interval / 1000.0 === 1 ? '' : 's');
	}

	if (resource === '$') {
		rateString = '$' + rate;  
	} else {
		rateString = rate + ' ' + resource;
	}



	return `${rateString} / ${intervalString}`;
}

function createAccumulator(name, interval, rate, intervalCoef,  resource='money') {	
	createAccumulatorInterface(name);

	const updateOutput = () => {draw('.' + name + '_output', calculateOutput(name));};

	initStoreValue(name, {
		interval,
		rate,
		resource,
		running: true,
	},
	[
		{
			triggers: [interval, rate],
			func: updateOutput
		}
	]);

	updateOutput();

	const cb = () => {
		var amount = getStoreValue(resource, 'value');

		updateStoreValue(resource, 'value', amount + getStoreValue(name, 'rate'));
	};

	onlineTicks(cb, name);

	return name;
}

function stopButton() {
	document.querySelector('.stopCounter').addEventListener('click', function () {
		updateStoreValue('exit', 'value', true);
		document.querySelector('.startCounter').disabled = false;
		document.querySelector('.stopCounter').disabled = true;
	});

	return;
}

function upgradeButton() {
	document.querySelector('.buyUpgrade').addEventListener('click', function () {
		var upgradeCost = getStoreValue('upgradeCost'),
			money = getStoreValue('money'),
			interval = getStoreValue('interval');

		if (upgradeCost > money) {
			draw('.warningText', 'Not enough money!');
			return;
		}

		draw('.warningText', '');

		updateStoreValue('money', money - upgradeCost);
		updateStoreValue('interval', interval - 50);
		updateStoreValue('upgradeCost', upgradeCost * 2);
	});

	return;
}

export {
	startButton,
	stopButton,
	upgradeButton
};
