import { getStoreValue } from '/utils/store.js';

import { MINIMUM_INTERVAL } from '/utils/globals.js';

function onlineTicks(cb, name) {
	var interval = getStoreValue(name, 'interval'),
		running = getStoreValue(name, 'running'),
		globalExit = getStoreValue('exit', 'value');

	if (interval < MINIMUM_INTERVAL) {
		console.warn('Attempted to use interval lower than minimum interval, setting interval to minimum');
		interval = MINIMUM_INTERVAL;
	}

	if (running !== true) {
		console.log('Ticks stopped');
		return;
	}

	if (globalExit === true) {
		console.log('All ticks stopped - global exit');
		return;
	}

	setTimeout(() => {
		cb();
		onlineTicks(...arguments);
	}, interval);

	return;
}

