import { drawFromStore } from '/utils/display.js';
import { startButton, stopButton, upgradeButton } from '/game/events.js';
import { initStoreValue, updateWholeStoreSubs, saveToLocalStorage } from '/utils/store.js';

import { activateDevFunctions } from '/utils/devtools.js';

function bindEvents() {
	startButton();
	stopButton();
	upgradeButton();

	return;
}

function initializeStore() {
	initStoreValue('money', {value: 0}, [{triggers: ['value'], func: () => drawFromStore('.money', 'money', 'value')}]);
	initStoreValue('upgradeCost', {value: 1}, [{triggers: ['value'], func: () => drawFromStore('.upgradeCost', 'upgradeCost', 'value')}]);
	initStoreValue('exit', {value: false});

	return;
}

function initializeDraw() {
	drawFromStore('.money', 'money', 'value');
	drawFromStore('.upgradeCost', 'upgradeCost', 'value');
	
	return;
}

function init() {
	console.log('Application started');
	
	bindEvents();

	initializeStore();
	initializeDraw();

	document.querySelector('.startCounter').disabled = false;
	document.querySelector('.stopCounter').disabled = true;

	activateDevFunctions();

	updateWholeStoreSubs([saveToLocalStorage]);

	return;
}

init();
